    override fun action(_localctx: RuleContext?, ruleIndex: Int, actionIndex: Int) {
        when (ruleIndex) {
            41 -> NEWLINE_action(_localctx, actionIndex)
            54 -> OPEN_PAREN_action(_localctx, actionIndex)
            55 -> CLOSE_PAREN_action(_localctx, actionIndex)
            61 -> OPEN_BRACK_action(_localctx, actionIndex)
            62 -> CLOSE_BRACK_action(_localctx, actionIndex)
            74 -> OPEN_BRACE_action(_localctx, actionIndex)
            75 -> CLOSE_BRACE_action(_localctx, actionIndex)
        }
    }

    fun NEWLINE_action(_localctx: RuleContext?, actionIndex: Int) {
        when (actionIndex) {
            0 -> {
                this.onNewLine();
            }
        }
    }

    fun OPEN_PAREN_action(_localctx: RuleContext?, actionIndex: Int) {
        when (actionIndex) {
            1 -> {
                this.openBrace();
            }
        }
    }

    fun CLOSE_PAREN_action(_localctx: RuleContext?, actionIndex: Int) {
        when (actionIndex) {
            2 -> {
                this.closeBrace();
            }
        }
    }

    fun OPEN_BRACK_action(_localctx: RuleContext?, actionIndex: Int) {
        when (actionIndex) {
            3 -> {
                this.openBrace();
            }
        }
    }

    fun CLOSE_BRACK_action(_localctx: RuleContext?, actionIndex: Int) {
        when (actionIndex) {
            4 -> {
                this.closeBrace();
            }
        }
    }

    fun OPEN_BRACE_action(_localctx: RuleContext?, actionIndex: Int) {
        when (actionIndex) {
            5 -> {
                this.openBrace();
            }
        }
    }

    fun CLOSE_BRACE_action(_localctx: RuleContext?, actionIndex: Int) {
        when (actionIndex) {
            6 -> {
                this.closeBrace();
            }
        }
    }

    override fun sempred(_localctx: RuleContext?, ruleIndex: Int, predIndex: Int): Boolean {
        when (ruleIndex) {
            41 -> return NEWLINE_sempred(_localctx, predIndex)
        }

        return true
    }

    @Suppress("UNSAFE_CALL")
    fun NEWLINE_sempred(_localctx: RuleContext?, predIndex: Int): Boolean {
        when (predIndex) {
            0 -> return (this.atStartOfInput())
        }

        return true
    }

